.dzu-inputLabelWithFiles {
    color: rgb(46, 73, 168);
}

progress {
    background-color: rgb(46, 73, 168) !important;
    border-radius: 0.5em;
    height: 0.5em;
    width: 10em;
}
progress::-webkit-progress-bar {
    background-color: rgb(46, 73, 168) !important;
    border-radius: 0.5em;
    height: 0.5em;
    width: 10em;
}
progress::-webkit-progress-value {
    background-color: rgb(46, 73, 168) !important;
    border-radius: 0.5em;
    height: 0.5em;
    width: 10em;
}
progress::-moz-progress-bar {
    background-color: rgb(46, 73, 168) !important;
    border-radius: 0.5em;
    height: 0.5em;
    width: 10em;
}
